body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  font-family: "Inter", sans-serif;
}

* {
  cursor: none;
}

div[class*=kursor].kursor--1 {
  left: 0px;
  top: 0px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 2px solid var(--primary-color);
  transition: all 0.2s ease, top 0.18s ease-out, left 0.18s ease-out;
}

div[class*=kursor].kursor--1 + .kursorChild {
  display: block;
  width: 15px;
  height: 15px;
  background: linear-gradient(155deg, rgb(79, 40, 195) 0%, rgb(175, 0, 255) 70%);
  border-radius: 50%;
  transition: all 0.2s ease, top 0s ease-out, left 0s ease-out;
}

@media screen and (max-width: 768px) {
  .notCursor * {
    cursor: auto;
  }
  div[class*=kursor] {
    display: block !important;
  }
  div[class*=kursorChild] {
    display: block !important;
  }
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
  margin: 0;
}

.button button {
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  transition: 0.5s all ease-in-out;
  background-color: var(--transparent-color);
  color: var(--primary-color);
}
.button button:hover {
  transition: 0.5s all ease-in-out;
}
@media (max-width: 768px) {
  .button button {
    padding: 10px;
  }
}
@media (max-width: 650px) {
  .button button {
    font-size: 14px;
  }
}

.button-outline {
  margin-top: 10px;
}
.button-outline button {
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  transition: 0.5s all ease-in-out;
  background-color: var(--transparent-color);
  color: var(--primary-color);
  font-weight: 600;
  border: 2px solid var(--primary-color);
}
.button-outline button:hover {
  transition: 0.5s all ease-in-out;
  background-color: var(--primary-color);
  color: var(--text-white);
}
@media (max-width: 768px) {
  .button-outline button {
    padding: 10px;
  }
}
@media (max-width: 650px) {
  .button-outline button {
    font-size: 14px;
  }
